/*wBreadcrumbs.scss*/
@import "../_config";
.breadcrumbs {
    width: 100%;
    display: table;
    font-size: 0;
    line-height: 0;
    margin-bottom: 30px;
    > span {
        display: inline-block;
        vertical-align: top;
        font-size: 14px;
        line-height: 18px;
        margin-right: 7px;
        color: #000;
        &:before {
            content: '/';
            display: inline-block;
            vertical-align: top;
            margin-right: 7px;
        }
        &:first-child:before {
            display: none;
        }
        > a {
            text-decoration: underline;
        }
    }
}
@media only screen and (max-width:620px) {
    .breadcrumbs {
        display: block;
        > span {
            display: none;
            &:before {
                display: none;
            }
            &:nth-last-child(2) {
                display: block;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                max-width: 100%;
                background: none;
                margin: 0;
                &:before {
                    content: '<';
                    display: inline-block;
                    margin: 0 5px 0 3px;
                }
            }
        }
    }
}