/*enterReg.scss*/
/*-------------------------------  enterPopup  ------------------------------------*/
.enterRegPopup {
    position: relative;
    width: auto;
    margin: 20px auto;
    padding: 1.5% 2%;
    max-width: 700px;
    background: #fff;
}

.enterReg_top {
    position: relative;
    margin-bottom: 3%;
    overflow: hidden;
}

.enterReg_top:after {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    width: 1px;
    height: 100%;
    background: #ddd;
}

.enterBlock, .regBlock {
    z-index: 9;
    float: left;
    position: relative;
    width: 46%;
    -webkit-backface-visibility: hidden;
}

.regBlock {
    float: right;
}

.enterReg_top .erTitle {
    position: relative;
    margin-bottom: 1.5em;
    text-transform: uppercase;
    font-size: 16px;
    font-weight: bold;
    line-height: 20px;
    color: #000;
}

.enterBlock_form {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    left: 0;
    -webkit-transition: opacity 0.8s ease;
    -moz-transition: opacity 0.8s ease;
    transition: opacity 0.8s ease;
}

.enterBlock .visForm {
    opacity: 1;
    visibility: visible;
    position: relative;
    transform: none;
}

.wFormRow {
    position: relative;
    margin-bottom: 8%;
}

.wFormRow input {
    top: 0;
    left: 0;
    box-sizing: border-box;
    width: 100%;
    height: 40px;
    padding: 10px 20px;
    border: 1px solid #ddcbd6;
    font-size: 16px;
    color: #000;
    background: #fff;
    -webkit-backface-visibility: hidden;
}

.wFormRow input:focus {
    border-color: #000;
}

.wFormRow input:focus::-webkit-input-placeholder {
    color: transparent;
}

.wFormRow input:focus::-moz-placeholder {
    color: transparent;
}

.wFormRow input:focus::-ms-placeholder {
    color: transparent;
}

.wFormRow .inpInfo {
    z-index: 2;
    position: absolute;
    top: 0;
    height: 0;
    overflow: hidden;
    right: 0;
    padding: 2px 5px;
    text-transform: uppercase;
    font-size: 9px;
    line-height: 12px;
    color: #fff;
    background: #000;
    opacity: 0;
    -webkit-transition: all 0.2s ease;
    -moz-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.wFormRow input:focus ~ .inpInfo {
    height: 12px;
    opacity: 1;
    -webkit-transform: none;
    -moz-transform: none;
    transform: none;
}

.checkBlock {
    display: inline-block;
    position: relative;
    margin-bottom: 7%;
    vertical-align: top;
    cursor: pointer;
    min-height: 32px;
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
}

.checkBlock input {
    height: 1px;
    width: 1px;
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.checkBlock ins {
    float: left;
    width: 14px;
    height: 14px;
    margin-right: 8px;
    border: 1px solid #ddcbd6;
    background: #fff;
}

input.error ~ ins {
    border-color: #f00;
}

.checkBlock span {
    display: block;
    overflow: hidden;
    font-size: 12px;
    line-height: 16px;
    color: #a0a0a0;
}

.checkBlock input:checked ~ ins {
    background: #000;
    box-shadow: inset 0 0 0 3px #fff;
}

.checkBlock:hover p {
    color: #333;
}

.enterReg_ctrl {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
}

.enterReg_btn {
    display: inline-block;
    vertical-align: top;
    background: #000;
    cursor: pointer;
    font-size: 15px;
    color: #fff;
    height: 36px;
    padding: 0 30px;
    border: none;
    text-transform: uppercase;
}

.enterReg_btn:hover {
    background: #666;
}

.enterReg_btn:active {
    -webkit-transform: translateY(2px);
    -moz-transform: translateY(2px);
    transform: translateY(2px);
    box-shadow:  inset 0 -2px 4px #fff, inset 0 3px 6px rgba(0,0,0,0.8);
    background: #666;
}

.passLink {
    position: absolute;
    left: 0;
    bottom: 7px;
    cursor: pointer;
    font-size: 14px;
    line-height: 18px;
}

.passLink:hover {
    text-decoration: underline;
}

.forgetInf {
    font-size: 14px;
    line-height: 1.5em;
    color: #999;
    min-height: 116px;
}

.socEnter {
    padding: 3% 0 0;
    border-top: 1px solid #ddd;
    text-align: center;
}

.socEnter .erTitle {
    display: inline-block;
    vertical-align: top;
    font-size: 15px;
    line-height: 30px;
    font-weight: bold;
    color: #000;
    text-transform: uppercase;
}

.socLinkEnter {
    display: inline-block;
    vertical-align: top;
    margin-left: 2%;
}

.socLinkEnter button {
    display: inline-block;
    vertical-align: top;
    border: none;
    width: 80px;
    height: 30px;
    margin-left: 10px;
    background-color: #000;
}

.socLinkEnter button:hover {
    background-color: #666;
}

.socLinkEnter button:active {
    background-color: #666;
    box-shadow: inset 0 -1px 3px rgba(255,255,255,0.8), inset 0 3px 6px rgba(0,0,0,0.4);
    -webkit-transform: translateY(1px);
    -moz-transform: translateY(1px);
    transform: translateY(1px);
}

.eVk {
    background: url(data:image/png;base64,) center center no-repeat;
}

.eFb {
    background: url(data:image/png;base64,) center center no-repeat;
}

.eOd {
    background: url(data:image/png;base64,) center center no-repeat;
}

.eMr {
    background: url(data:image/png;base64,) center center no-repeat;
}


@media only screen and (max-width:720px) {
    .popupContent {
        display: block;
        overflow: hidden;
        margin: 0;
        max-height: 0;
        padding: 0 10px;
        -webkit-transition: all 0.5s linear;
        -moz-transition: all 0.5s linear;
        transition: all 0.5s linear;
    }
    .enterRegPopup {
        max-width: 380px;
        padding: 30px 15px 15px;
    }
    .enterRegPopup .mfp-close {
        width: 30px;
        height: 30px;
        line-height: 30px;
    }
    .enterReg_top {
        margin: 0;
    }
    .enterReg_top:after {
        display: none;
    }
    .socEnter {
        padding: 0;
        border: 0;
    }
    .enterBlock, .regBlock {
        float: none;
        width: auto;
    }
    .enterReg_top .erTitle, .socEnter .erTitle {
        margin: 0;
        background: #000;
        color: #fff;
        display: block;
        text-align: center;
        padding: 10px 0;
        font-size: 14px;
        line-height: 20px;
        text-transform: uppercase;
        margin-bottom: 1px;
        cursor: pointer;
    }
    .wCur .popupContent {
        max-height: 400px;
        padding: 10px;
    }
    .wCur .erTitle {
        box-shadow: inset 0 0 0 1px #000;
        background: #fff;
        color: #000;
    }
    .socLinkEnter button {
        width: 48%;
        float: left;
        margin: 1%;
        height: 40px;
    }
}